:root {
  --alpheios-base-font-size: 16px;
  --alpheios-color-neutral-dark: #757575;
  --alpheios-color-neutral-light: #E6E6E6;
  --alpheios-color-neutral-lightest: #FFF;
  --alpheios-color-dark: #0E2233;
  --alpheios-color-dark-hover: #1B374F;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark);
  --alpheios-color-muted: #185F6D;
  --alpheios-color-muted-hover: #124D59;
  --alpheios-color-muted-pressed: #093943;
  --alpheios-color-vivid: #C64906;
  --alpheios-color-vivid-hover: #F27431;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant);
  --alpheios-color-bright: #FFC24F;
  --alpheios-color-bright-hover: #FFD27D;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright);
  --alpheios-color-light: #BCE5F0;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest);
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid);
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid);
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-lookup__form {
  margin: 30px 5px 5px;
  text-align: left;
}

.alpheios-input.alpheios-lookup__input {
  width: 70%;
  margin-bottom: 10px;
  vertical-align: top;
}
.alpheios-input.alpheios-lookup__input:focus {
  border-color: #5BC8DC;
}

.alpheios-lookup__settings {
  text-align: left;
}

a.alpheios-lookup__settings-link {
  display: block;
  padding-top: 5px;
}

.alpheios-lookup__button_with_link {
  width: 29%;
}

.alpheios-panel .alpheios-lookup__form {
  width: 100%;
  margin: 5px 0;
}

.alpheios-panel__options-item {
  max-width: none;
}

.alpheios-panel__options-item .alpheios-select:not([multiple]):not([size]),
.alpheios-panel__options-item .alpheios-select[multiple],
.alpheios-panel__options-item .alpheios-select[size] {
  max-width: 250px;
}

.alpheios-override-lang {
  margin-bottom: 10px;
}

.alpheios-override-lang__label {
  padding-bottom: 10px;
}