:root {
  --alpheios-base-font-size: 16px;
  --alpheios-color-neutral-dark: #757575;
  --alpheios-color-neutral-light: #E6E6E6;
  --alpheios-color-neutral-lightest: #FFF;
  --alpheios-color-dark: #0E2233;
  --alpheios-color-dark-hover: #1B374F;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark);
  --alpheios-color-muted: #185F6D;
  --alpheios-color-muted-hover: #124D59;
  --alpheios-color-muted-pressed: #093943;
  --alpheios-color-vivid: #C64906;
  --alpheios-color-vivid-hover: #F27431;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant);
  --alpheios-color-bright: #FFC24F;
  --alpheios-color-bright-hover: #FFD27D;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright);
  --alpheios-color-light: #BCE5F0;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest);
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid);
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid);
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-navmenu {
  position: absolute;
  z-index: 1000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #FFF;
  display: flex;
  flex-direction: column;
}

.alpheios-navmenu__item {
  display: flex;
  padding: 10px 20px 10px 30px;
  border-bottom: 1px solid #D1D1D0;
  cursor: pointer;
}

.alpheios-navmenu__item:hover,
.alpheios-navmenu__item:focus {
  color: #5BC8DC;
}

.alpheios-navmenu__item .alpheios-navbuttons__icon,
.alpheios-navmenu__item.active:hover .alpheios-navbuttons__icon,
.alpheios-navmenu__item.active:focus .alpheios-navbuttons__icon {
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-navmenu__item:hover .alpheios-navbuttons__icon,
.alpheios-navmenu__item:focus .alpheios-navbuttons__icon,
.alpheios-navmenu__item.active .alpheios-navbuttons__icon {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}

.alpheios-navbuttons__icon-cont {
  flex-grow: 0;
  width: 40px;
  height: 40px;
  margin-right: 40px;
}

.alpheios-navbuttons__icon {
  width: 40px;
  height: 40px;
}

.alpheios-navmenu div.alpheios-navmenu__text {
  font-size: 20px;
  line-height: 1.2;
  top: 10px;
  position: relative;
}