:root {
  --alpheios-base-font-size: 16px;
  --alpheios-color-neutral-dark: #757575;
  --alpheios-color-neutral-light: #E6E6E6;
  --alpheios-color-neutral-lightest: #FFF;
  --alpheios-color-dark: #0E2233;
  --alpheios-color-dark-hover: #1B374F;
  --alpheios-color-dark-pressed: var(--alpheios-color-dark);
  --alpheios-color-muted: #185F6D;
  --alpheios-color-muted-hover: #124D59;
  --alpheios-color-muted-pressed: #093943;
  --alpheios-color-vivid: #C64906;
  --alpheios-color-vivid-hover: #F27431;
  --alpheios-color-vivid-pressed: var(--alpheios-color-vibrant);
  --alpheios-color-bright: #FFC24F;
  --alpheios-color-bright-hover: #FFD27D;
  --alpheios-color-bright-pressed: var(--alpheios-color-bright);
  --alpheios-color-light: #BCE5F0;
  --alpheios-btn-primary-font-color: var(--alpheios-color-neutral-lightest);
  --alpheios-btn-primary-bg-color: var(--alpheios-color-vivid);
  --alpheios-btn-primary-border-color: var(--alpheios-color-vivid);
}

/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
#alpheios-reskin-panel.alpheios-skin_panel {
  display: inline-block;
}

ul.alpheios-skin_properties {
  margin: 0;
  padding: 0 30px 10px 10px;
  list-style: none;
  max-width: 250px;
  display: inline-block;
}

#alpheios-reskin-panel .alpheios-skin_properties_item {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  color: #fff;
  background: #4E6476;
  cursor: pointer;
  padding: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  text-indent: 0;
  list-style-type: none;
}

#alpheios-reskin-panel .alpheios-skin_properties_item__light_bg,
#alpheios-reskin-panel .alpheios-skin_properties_item__dark_bg {
  border-radius: 0;
  background: transparent;
}
#alpheios-reskin-panel .alpheios-skin_properties_item__light_bg svg,
#alpheios-reskin-panel .alpheios-skin_properties_item__dark_bg svg {
  width: 90%;
  height: 90%;
}

.alpheios-font_small_class #alpheios-reskin-panel .alpheios-skin_properties_item__small_font {
  background: #7E8897;
}

.alpheios-font_medium_class #alpheios-reskin-panel .alpheios-skin_properties_item__medium_font {
  background: #7E8897;
}

.alpheios-font_large_class #alpheios-reskin-panel .alpheios-skin_properties_item__large_font {
  background: #7E8897;
}